import React, { useState } from 'react';
import { IoTriangle } from "react-icons/io5";

const scheduleData = [
  {
    date: ', 22  2023',
    events: [
      { time: '15:00', title: '', description: '          .' },
      { time: '17:00', title: '   ', description: '         .' },
    ],
  },
  {
    date: ', 23  2023',
    events: [
      { time: '10:00', title: '', description: ' .       ,  .' },
      { time: '11:00', title: 'C', description: '     .' },
      { time: '13:00', title: '', description: '        .' },
      { time: '14:00', title: '', description: '  ,      .' },
      { time: '15:00', title: ' ', description: '  ,     .' },
      { time: '15:30', title: '', description: '      .' },
      { time: '16:30', title: ' ', description: '       .' },
      { time: '17:00', title: '', description: '       .' },
    ],
  },
  {
    date: ', 24  2023',
    events: [
      { time: '11:00', title: '', description: '       .' },
      { time: '13:00', title: '', description: ' ,    .' },
      { time: '14:00', title: '', description: '    .' },
      { time: '16:00', title: '.  ', description: '        .' },
    ],
  },
];


const ConferenceSchedule = () => {
  const [currentIdx, setCurrentIdx] = useState(0);
  const handlePrev = () => {
    if(currentIdx > 0) {
      setCurrentIdx(currentIdx - 1)
    }
  }
  const handleNext = () => {
    if(currentIdx < scheduleData.length - 1) {
      setCurrentIdx(currentIdx + 1)
    }
  }
  return (
    <div className="container text-white p-10">
      <h1 className="text-xl sm:text-3xl lg:text-6xl font-extrabold text-center mb-12">Conference Schedule</h1>
      {/* eslint-disable-next-line */}
      {scheduleData.map((day, idx) => {
        if(idx === currentIdx) {
          return <div key={day.date} className="mb-8 lg:w-1/2 mx-auto">
            <div className="flex gap-4 justify-center items-center mb-6">
              <IoTriangle onClick={handlePrev} color="orange" size="30" className="-rotate-90"/>
              <h2 className="text-xl md:text-3xl font-bold  text-center">{day.date}</h2>
              <IoTriangle onClick={handleNext} color="orange" size="30" className="rotate-90"/>
            </div>
            {day.events.map((event, idx) => (
              <div key={idx} className=" flex flex-row-reverse gap-7 md:gap-14 items-center mb-4 w-full p-6 md:p-12 bg-[#2D3640]">
                <div className="flex-grow">
                  <h3 className="text-md md:text-xl font-semibold">{event.title}</h3>
                  <p className="text-sm md:text-md">{event.description}</p>
                </div>
                <span className="w-auto text-md md:text-xl font-bold text-center">{event.time}</span>
              </div>
            ))}

          </div>

        }
      })}
         </div>
  );
};

export default ConferenceSchedule;
